.item {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    border-bottom: 1px solid #fff;
    padding: 10px;
}
.item > *:first-child {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: .85rem;
    color: #555;
}
.icon {
    min-width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 6px;
}
.icon > * {
    width: 100%;
    height: 100%;
}
.container {
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 3px;
    
}
.value {
    font-size: 1.1rem;
    text-align: left;
}
.unit {
    font-size: .8rem;
}